.button {
  display: block;
  box-sizing: border-box;
  padding: 10px 16px;
  background-color: navy;
  color: white;
  font-family: Arial, sans-serif;
  font-weight: bold;
  font-size: 16px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: 0.5s;
  border-radius: 6px;
  text-decoration: none;
  text-align: center;
}

.button:hover {
  background: transparent;
  color: navy;
  border-color: navy;
}

.button:focus {
  outline: none;
  box-shadow: 0 0 0 4px blue;
}