.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  flex: 1 1 auto;
}

.button {
  display: block;
  box-sizing: border-box;
  padding: 18px 32px;
  min-width: 180px;
  background-color: navy;
  color: white;
  font-family: Arial, sans-serif;
  font-weight: bold;
  font-size: 16px;
  cursor: pointer;
  text-decoration: none;
  text-align: center;
  border: 1px solid transparent;
  transition: 0.5s;
  border-radius: 6px;
}

.button:hover {
  background: transparent;
  color: navy;
  border-color: navy;
}

.button:focus {
  outline: none;
  box-shadow: 0 0 0 4px blue;
}

.button:not(:first-child) {
  margin-top: 20px;
}